import React, { useState, useEffect } from "react";
import "./style.css";

export default function App() {
  const [showResult, setShowResult] = useState(false);
  const [boy, setBoy] = useState();
  const [girl, setGirl] = useState();
  const [boyList, setBoyList] = useState([]);
  const [girlList, setGirlList] = useState([]);

  useEffect(() => {
    if (!boyList.length && !girlList.length) {
      setShowResult(false);
    }
  }, [boyList, girlList]);

  useEffect(() => {
    if (localStorage.getItem("boys")) {
      setBoyList(JSON.parse(localStorage.getItem("boys")));
    }
    if (localStorage.getItem("girls")) {
      setGirlList(JSON.parse(localStorage.getItem("girls")));
    }
  }, []);

  const addBoy = (e) => {
    e.preventDefault();
    setBoyList([...boyList, boy]);
    localStorage.setItem("boys", JSON.stringify([...boyList, boy]));
    setBoy("");
  };

  const removeBoy = (index) => {
    const list = [...boyList];
    list.splice(index, 1);
    setBoyList(list);
    localStorage.setItem("boys", JSON.stringify(list));
  };

  const addGirl = (e) => {
    e.preventDefault();
    setGirlList([...girlList, girl]);
    localStorage.setItem("girls", JSON.stringify([...girlList, girl]));
    setGirl("");
  };

  const removeGirl = (index) => {
    const list = [...girlList];
    list.splice(index, 1);
    setGirlList(list);
    localStorage.setItem("girls", JSON.stringify(list));
  };
  return (
    <div className="grid grid-cols-3 gap-4">
      <div className="p-4">
        <h1 className="font-mono font-bold text-6xl text-blue-400">
          Blue {boyList.length}
        </h1>
        <div className="p-4">
          {boyList.map((name, index) => (
            <div
              key={`boy-${index}`}
              className="capitalize font-mono font-bold text-3xl"
            >
              {name}{" "}
              <span className="text-gray-400" onClick={() => removeBoy(index)}>
                &times;
              </span>
            </div>
          ))}
        </div>
        <form className="form" onSubmit={addBoy}>
          <div className="flex">
            <input
              value={boy}
              onChange={(e) => setBoy(e.currentTarget.value)}
              className="input bg-gray-200 appearance-none w-full px-3 py-3  pb-2 focus focus:border-indigo-600 focus:outline-none active:outline-none active:border-indigo-600"
              id="name"
              type="text"
              autoComplete="off"
            />
            <button
              onClick={addBoy}
              className="bg-blue-400 text-white font-bold py-3 px-6"
            >
              Add
            </button>
          </div>
        </form>
      </div>
      <div className="py-10 flex justify-center">
        <div
          onDoubleClick={() => setShowResult(true)}
          className={showResult ? "pink" : ""}
          id="heart"
        ></div>
      </div>
      <div className="p-4">
        <h1 className="font-mono font-bold text-6xl text-pink-400">
          Pink {girlList.length}
        </h1>
        <div className="p-4">
          {girlList.map((name, index) => (
            <div
              key={`girl-${index}`}
              className="capitalize font-mono font-bold text-3xl"
            >
              {name}{" "}
              <span className="text-gray-400" onClick={() => removeGirl(index)}>
                &times;
              </span>
            </div>
          ))}
        </div>
        <form className="form" onSubmit={addGirl}>
          <div className="flex">
            <input
              value={girl}
              onChange={(e) => setGirl(e.currentTarget.value)}
              className="input bg-gray-200 appearance-none w-full px-3 py-3  pb-2 focus focus:border-indigo-600 focus:outline-none active:outline-none active:border-indigo-600"
              id="name"
              type="text"
              autoComplete="off"
            />
            <button className="bg-pink-400 text-white font-bold py-3 px-6">
              Add
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
